/* Global Auth Styles */
html, body {
  scroll-behavior: auto !important;
  overflow-x: hidden;
}

/* Prevent scrolling issues */
.auth-page-wrapper {
  height: 100vh;
  overflow: hidden;
}

/* Auth Container */
.auth-container {
  min-height: 100vh;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem 1rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  position: relative;
  overflow-y: auto;
  box-sizing: border-box;
}

.auth-background {
  position: relative;
  z-index: 1;
  width: 100%;
  max-width: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.auth-background::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(255,255,255,0.1) 0%, transparent 70%);
  animation: float 6s ease-in-out infinite;
  z-index: -1;
}

@keyframes float {
  0%, 100% {
    transform: translate(0, 0) rotate(0deg);
  }
  33% {
    transform: translate(30px, -30px) rotate(120deg);
  }
  66% {
    transform: translate(-20px, 20px) rotate(240deg);
  }
}

/* Auth Card */
.auth-card {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  border-radius: 24px;
  padding: 3rem;
  box-shadow: 
    0 25px 50px rgba(0, 0, 0, 0.15),
    0 0 0 1px rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.2);
  animation: slideIn 0.5s ease-out;
  position: relative;
  scroll-behavior: auto;
  min-height: 500px;
}

.register-card {
  max-width: 600px;
  min-height: 600px;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Auth Header */
.auth-header {
  text-align: center;
  margin-bottom: 3rem;
}

.auth-logo {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  margin-bottom: 1.5rem;
}

.logo-icon {
  font-size: 2.5rem;
}

.auth-logo h1 {
  margin: 0;
  font-size: 2.2rem;
  font-weight: bold;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.auth-header h2 {
  margin: 0 0 0.75rem 0;
  color: #333;
  font-size: 1.8rem;
  font-weight: 600;
}

.auth-header p {
  margin: 0;
  color: #666;
  font-size: 1.1rem;
  font-size: 0.9rem;
}

/* Error Message */
.error-message {
  background: #fff5f5;
  border: 1px solid #fed7d7;
  border-radius: 8px;
  padding: 0.75rem 1rem;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  color: #c53030;
  font-size: 0.9rem;
}

.error-icon {
  margin-right: 0.5rem;
}

/* Auth Form */
.auth-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  margin-bottom: 0.75rem;
  color: #333;
  font-weight: 500;
  font-size: 1rem;
}

.form-group input,
.form-group select {
  padding: 1rem 1.25rem;
  border: 2px solid #e2e8f0;
  border-radius: 12px;
  font-size: 1.1rem;
  transition: all 0.2s;
  background: white;
  min-height: 24px;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.form-group input:disabled,
.form-group select:disabled {
  background: #f7fafc;
  cursor: not-allowed;
}

.form-group select {
  cursor: pointer;
}

/* Password Strength */
.password-strength {
  margin-top: 0.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.strength-bar {
  flex: 1;
  height: 4px;
  background: #e2e8f0;
  border-radius: 2px;
  overflow: hidden;
}

.strength-fill {
  height: 100%;
  transition: all 0.3s;
  border-radius: 2px;
}

.strength-text {
  font-size: 0.8rem;
  font-weight: 500;
  min-width: 60px;
}

/* Field Error */
.field-error {
  margin-top: 0.5rem;
  color: #e53e3e;
  font-size: 0.8rem;
}

/* Form Options */
.form-options {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0.5rem 0;
}

.checkbox-label {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 0.9rem;
  color: #333;
}

.checkbox-label input {
  margin: 0;
  opacity: 0;
  position: absolute;
}

.checkmark {
  width: 18px;
  height: 18px;
  border: 2px solid #e2e8f0;
  border-radius: 4px;
  margin-right: 0.5rem;
  position: relative;
  transition: all 0.2s;
}

.checkbox-label input:checked + .checkmark {
  background: #667eea;
  border-color: #667eea;
}

.checkbox-label input:checked + .checkmark::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 12px;
  font-weight: bold;
}

.forgot-password,
.terms-link {
  color: #667eea;
  text-decoration: none;
  font-size: 0.9rem;
  transition: color 0.2s;
}

.forgot-password:hover,
.terms-link:hover {
  color: #553c9a;
  text-decoration: underline;
}

/* Auth Button */
.auth-button {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 12px;
  padding: 1.25rem 2rem;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  min-height: 56px;
  margin-top: 0.5rem;
}

.auth-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(102, 126, 234, 0.3);
}

.auth-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.auth-button.loading {
  cursor: not-allowed;
}

.spinner {
  width: 20px;
  height: 20px;
  border: 2px solid transparent;
  border-top: 2px solid currentColor;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Auth Divider */
.auth-divider {
  margin: 1.5rem 0;
  text-align: center;
  position: relative;
}

.auth-divider::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  height: 1px;
  background: #e2e8f0;
}

.auth-divider span {
  background: rgba(255, 255, 255, 0.95);
  padding: 0 1rem;
  color: #666;
  font-size: 0.9rem;
  position: relative;
}

/* Social Login */
.social-login {
  display: flex;
  gap: 1rem;
  margin-top: 0.5rem;
}

.social-button {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  padding: 1rem;
  border: 2px solid #e2e8f0;
  border-radius: 12px;
  background: white;
  cursor: pointer;
  transition: all 0.2s;
  font-size: 1rem;
  font-weight: 500;
  color: #333;
  min-height: 56px;
}

.social-button:hover {
  border-color: #cbd5e0;
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.social-button.google:hover {
  border-color: #4285f4;
  color: #4285f4;
}

.social-button.github:hover {
  border-color: #333;
  color: #333;
}

/* Auth Footer */
.auth-footer {
  text-align: center;
  margin-top: 2rem;
  padding-top: 1.5rem;
  border-top: 1px solid #e2e8f0;
}

.auth-footer p {
  margin: 0;
  color: #666;
  font-size: 1rem;
}

.auth-link {
  color: #667eea;
  text-decoration: none;
  font-weight: 500;
  margin-left: 0.25rem;
  transition: color 0.2s;
}

.auth-link:hover {
  color: #553c9a;
  text-decoration: underline;
}

/* Desktop Optimization */
@media (min-width: 769px) {
  .auth-page-wrapper {
    height: 100vh;
    overflow: hidden;
  }
  
  .auth-container {
    align-items: center;
    justify-content: center;
    padding: 2rem;
    height: 100vh;
    overflow: hidden;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  
  .auth-background {
    margin: 0;
    position: static;
    max-width: 600px;
  }
  
  .auth-card {
    max-height: 85vh;
    overflow-y: auto;
    scroll-behavior: smooth;
    padding: 3.5rem;
    min-height: 550px;
  }
  
  .register-card {
    max-width: 650px;
    max-height: 90vh;
    overflow-y: auto;
    scroll-behavior: smooth;
    min-height: 650px;
  }
}

/* Tablet and Mobile Responsive */
@media (max-width: 768px) {
  .auth-container {
    padding: 1.5rem 1rem;
    align-items: flex-start;
    justify-content: flex-start;
    padding-top: 3rem;
  }
  
  .auth-background {
    margin: 0;
  }
}

/* Mobile Responsive */
@media (max-width: 480px) {
  .auth-container {
    padding: 1rem 0.5rem;
    align-items: flex-start;
    justify-content: flex-start;
    min-height: 100vh;
    overflow-y: auto;
    padding-top: 2rem;
  }

  .auth-background {
    margin: 0;
    width: 100%;
  }

  .auth-card {
    padding: 1.5rem;
    border-radius: 16px;
    width: 100%;
    max-width: none;
    margin: 0;
    max-height: none;
    overflow-y: visible;
  }

  .register-card {
    max-width: none;
    max-height: none;
    overflow-y: visible;
  }

  .auth-header h2 {
    font-size: 1.3rem;
  }

  .form-group input,
  .form-group select {
    padding: 0.6rem 0.8rem;
  }

  .social-login {
    flex-direction: column;
  }

  .form-options {
    flex-direction: column;
    align-items: stretch;
    gap: 0.75rem;
  }

  .checkbox-label {
    font-size: 0.8rem;
  }
}

/* Accessibility */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* High contrast mode */
@media (prefers-contrast: high) {
  .auth-card {
    background: white;
    border: 2px solid #000;
  }
  
  .form-group input,
  .form-group select {
    border-color: #000;
  }
  
  .auth-button {
    background: #000;
    border: 2px solid #000;
  }
}
