/* Document Editor Container */
.document-editor {
  display: flex;
  height: calc(100vh - 60px);
  background: #f8f9fa;
}

/* Documents Sidebar */
.documents-sidebar {
  width: 280px;
  background: white;
  border-right: 1px solid #e2e8f0;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
}

.sidebar-header {
  padding: 1.5rem 1rem;
  border-bottom: 1px solid #f1f5f9;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.sidebar-header h2 {
  margin: 0;
  font-size: 1.2rem;
  color: #2d3748;
  font-weight: 600;
}

.new-doc-btn {
  background: #667eea;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  font-size: 0.85rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.new-doc-btn:hover {
  background: #553c9a;
  transform: translateY(-1px);
}

.search-documents {
  padding: 1rem;
  border-bottom: 1px solid #f1f5f9;
}

.search-input {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  font-size: 0.9rem;
  transition: border-color 0.2s;
}

.search-input:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.documents-list {
  flex: 1;
  overflow-y: auto;
  padding: 0.5rem;
}

.document-item {
  padding: 1rem;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;
  margin-bottom: 0.5rem;
  border: 1px solid transparent;
}

.document-item:hover {
  background: #f7fafc;
  border-color: #e2e8f0;
}

.document-item.active {
  background: #ebf4ff;
  border-color: #667eea;
}

.doc-title {
  font-weight: 600;
  color: #2d3748;
  margin-bottom: 0.5rem;
  font-size: 0.95rem;
  line-height: 1.3;
}

.doc-meta {
  display: flex;
  justify-content: space-between;
  font-size: 0.8rem;
  color: #718096;
  margin-bottom: 0.5rem;
}

.doc-collaborators {
  display: flex;
  justify-content: flex-end;
}

.collaborator-count {
  font-size: 0.75rem;
  color: #667eea;
  background: #ebf4ff;
  padding: 0.125rem 0.5rem;
  border-radius: 10px;
}

/* Editor Main */
.editor-main {
  flex: 1;
  display: flex;
  flex-direction: column;
  background: white;
  min-width: 0;
}

/* No Document State */
.no-document {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  background: #fafafa;
}

.no-doc-content {
  text-align: center;
  max-width: 400px;
  padding: 2rem;
}

.no-doc-content h3 {
  margin: 0 0 1rem 0;
  color: #4a5568;
  font-size: 1.5rem;
}

.no-doc-content p {
  margin: 0 0 2rem 0;
  color: #718096;
  line-height: 1.6;
}

.create-first-doc {
  background: #667eea;
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.create-first-doc:hover {
  background: #553c9a;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
}

/* Editor Header */
.editor-header {
  padding: 1rem 1.5rem;
  border-bottom: 1px solid #e2e8f0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  flex-shrink: 0;
}

.doc-title-section {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex: 1;
}

.title-input {
  font-size: 1.5rem;
  font-weight: 600;
  border: none;
  background: none;
  color: #2d3748;
  padding: 0.5rem;
  border-radius: 4px;
  transition: background 0.2s;
  min-width: 250px;
}

.title-input:focus {
  outline: none;
  background: #f7fafc;
}

.save-status {
  margin-left: 1rem;
}

.status-indicator {
  font-size: 0.85rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.status-indicator.saved {
  color: #38a169;
}

.status-indicator.saving {
  color: #ed8936;
}

.status-indicator.editing {
  color: #667eea;
}

.editor-actions {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.collaborators-list {
  display: flex;
  gap: -0.25rem;
}

.collaborator-avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8rem;
  font-weight: 600;
  border: 2px solid white;
  margin-left: -0.25rem;
  position: relative;
  z-index: 1;
}

.collaborator-avatar:first-child {
  margin-left: 0;
}

.comments-toggle {
  background: #f7fafc;
  border: 1px solid #e2e8f0;
  border-radius: 6px;
  padding: 0.5rem 0.75rem;
  cursor: pointer;
  font-size: 0.85rem;
  transition: all 0.2s;
}

.comments-toggle:hover {
  background: #edf2f7;
  border-color: #cbd5e0;
}

.save-btn {
  background: #38a169;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  font-size: 0.85rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.save-btn:hover:not(:disabled) {
  background: #2f855a;
}

.save-btn:disabled {
  background: #a0aec0;
  cursor: not-allowed;
}

/* Editor Toolbar */
.editor-toolbar {
  padding: 0.75rem 1.5rem;
  border-bottom: 1px solid #f1f5f9;
  display: flex;
  align-items: center;
  gap: 1rem;
  background: #fafafa;
  flex-wrap: wrap;
  flex-shrink: 0;
}

.format-group {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  padding: 0.25rem;
  border-radius: 6px;
  background: white;
  border: 1px solid #e2e8f0;
}

.format-btn {
  width: 32px;
  height: 32px;
  border: none;
  background: none;
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.85rem;
  font-weight: 500;
  transition: all 0.2s;
  color: #4a5568;
}

.format-btn:hover {
  background: #f7fafc;
  color: #2d3748;
}

.format-btn:active {
  background: #edf2f7;
}

/* Editor Container */
.editor-container {
  flex: 1;
  display: flex;
  position: relative;
  overflow: hidden;
}

.editor-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  position: relative;
}

.rich-editor {
  flex: 1;
  padding: 2rem;
  overflow-y: auto;
  background: white;
  line-height: 1.6;
  font-size: 1rem;
  color: #2d3748;
  outline: none;
  min-height: 500px;
  border: 2px solid transparent;
  border-radius: 8px;
  font-family: Georgia, serif;
  white-space: pre-wrap;
  word-wrap: break-word;
  cursor: text;
  -webkit-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  user-select: text;
}

.rich-editor:focus {
  border-color: #007bff;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

.rich-editor:empty::before {
  content: attr(data-placeholder);
  color: #a0aec0;
  font-style: italic;
  pointer-events: none;
}

.rich-editor h1 {
  font-size: 2rem;
  font-weight: 700;
  margin: 2rem 0 1rem 0;
  color: #1a202c;
  line-height: 1.2;
}

.rich-editor h2 {
  font-size: 1.5rem;
  font-weight: 600;
  margin: 1.5rem 0 0.75rem 0;
  color: #2d3748;
  line-height: 1.3;
}

.rich-editor h3 {
  font-size: 1.25rem;
  font-weight: 600;
  margin: 1.25rem 0 0.5rem 0;
  color: #4a5568;
  line-height: 1.4;
}

.rich-editor p {
  margin: 0 0 1rem 0;
}

.rich-editor ul, .rich-editor ol {
  margin: 1rem 0;
  padding-left: 2rem;
}

.rich-editor li {
  margin: 0.5rem 0;
}

.rich-editor table {
  width: 100%;
  border-collapse: collapse;
  margin: 1.5rem 0;
  border: 1px solid #e2e8f0;
}

.rich-editor th, .rich-editor td {
  padding: 0.75rem;
  border: 1px solid #e2e8f0;
  text-align: left;
}

.rich-editor th {
  background: #f7fafc;
  font-weight: 600;
}

.rich-editor img {
  max-width: 100%;
  height: auto;
  margin: 1rem 0;
  border-radius: 4px;
}

.rich-editor a {
  color: #667eea;
  text-decoration: underline;
}

.rich-editor pre {
  background: #f7fafc;
  padding: 1rem;
  border-radius: 6px;
  overflow-x: auto;
  margin: 1rem 0;
}

.rich-editor code {
  background: #edf2f7;
  padding: 0.125rem 0.25rem;
  border-radius: 3px;
  font-family: 'Courier New', monospace;
  font-size: 0.9em;
}

/* Comment Panel */
.comment-panel {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 300px;
  background: white;
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  padding: 1rem;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.15);
  z-index: 100;
}

.selected-text {
  background: #f0f4f8;
  padding: 0.5rem;
  border-radius: 4px;
  font-style: italic;
  margin-bottom: 0.75rem;
  font-size: 0.9rem;
  color: #4a5568;
}

.comment-panel textarea {
  width: 100%;
  border: 1px solid #e2e8f0;
  border-radius: 6px;
  padding: 0.75rem;
  font-size: 0.9rem;
  resize: vertical;
  min-height: 80px;
  margin-bottom: 0.75rem;
}

.comment-panel textarea:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.comment-actions {
  display: flex;
  gap: 0.5rem;
  justify-content: flex-end;
}

.comment-actions button {
  padding: 0.5rem 1rem;
  border: 1px solid #e2e8f0;
  border-radius: 6px;
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.2s;
}

.comment-actions button:first-child {
  background: #667eea;
  color: white;
  border-color: #667eea;
}

.comment-actions button:first-child:hover:not(:disabled) {
  background: #553c9a;
}

.comment-actions button:first-child:disabled {
  background: #a0aec0;
  border-color: #a0aec0;
  cursor: not-allowed;
}

.comment-actions button:last-child {
  background: white;
  color: #4a5568;
}

.comment-actions button:last-child:hover {
  background: #f7fafc;
}

/* Comments Sidebar */
.comments-sidebar {
  width: 320px;
  background: white;
  border-left: 1px solid #e2e8f0;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
}

.comments-header {
  padding: 1rem 1.5rem;
  border-bottom: 1px solid #f1f5f9;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.comments-header h3 {
  margin: 0;
  font-size: 1.1rem;
  color: #2d3748;
  font-weight: 600;
}

.close-comments {
  background: none;
  border: none;
  font-size: 1.5rem;
  color: #a0aec0;
  cursor: pointer;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
}

.close-comments:hover {
  background: #f7fafc;
  color: #4a5568;
}

.comments-list {
  flex: 1;
  overflow-y: auto;
  padding: 1rem;
}

.no-comments {
  text-align: center;
  padding: 2rem 1rem;
  color: #718096;
  font-size: 0.9rem;
  line-height: 1.5;
}

.comment {
  background: #f7fafc;
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1rem;
}

.comment-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.comment-author {
  font-weight: 600;
  color: #2d3748;
  font-size: 0.85rem;
}

.comment-time {
  font-size: 0.75rem;
  color: #a0aec0;
}

.comment-text {
  background: #edf2f7;
  padding: 0.5rem;
  border-radius: 4px;
  font-style: italic;
  margin-bottom: 0.75rem;
  font-size: 0.85rem;
  color: #4a5568;
}

.comment-content {
  color: #2d3748;
  line-height: 1.5;
  margin-bottom: 0.75rem;
  font-size: 0.9rem;
}

.comment-actions {
  display: flex;
  justify-content: flex-end;
}

.resolve-btn {
  background: #38a169;
  color: white;
  border: none;
  padding: 0.375rem 0.75rem;
  border-radius: 4px;
  font-size: 0.8rem;
  cursor: pointer;
  transition: all 0.2s;
}

.resolve-btn:hover {
  background: #2f855a;
}

/* Modal */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal {
  background: white;
  border-radius: 12px;
  width: 90%;
  max-width: 500px;
  max-height: 90vh;
  overflow: hidden;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
}

.modal-header {
  padding: 1.5rem;
  border-bottom: 1px solid #f1f5f9;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-header h3 {
  margin: 0;
  font-size: 1.2rem;
  color: #2d3748;
  font-weight: 600;
}

.close-modal {
  background: none;
  border: none;
  font-size: 1.5rem;
  color: #a0aec0;
  cursor: pointer;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
}

.close-modal:hover {
  background: #f7fafc;
  color: #4a5568;
}

.modal-content {
  padding: 1.5rem;
  max-height: 60vh;
  overflow-y: auto;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 600;
  color: #2d3748;
  font-size: 0.9rem;
}

.form-group input {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #e2e8f0;
  border-radius: 6px;
  font-size: 0.95rem;
  transition: border-color 0.2s;
}

.form-group input:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.template-options {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.75rem;
  margin-top: 0.5rem;
}

.template-option {
  display: flex;
  align-items: center;
  padding: 1rem;
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;
}

.template-option:hover {
  border-color: #cbd5e0;
  background: #f7fafc;
}

.template-option input[type="radio"] {
  margin-right: 0.75rem;
  width: auto;
}

.template-option input[type="radio"]:checked + .template-info {
  color: #667eea;
}

.template-option:has(input:checked) {
  border-color: #667eea;
  background: #ebf4ff;
}

.template-name {
  font-weight: 500;
  color: #2d3748;
  font-size: 0.9rem;
}

.modal-actions {
  padding: 1rem 1.5rem;
  border-top: 1px solid #f1f5f9;
  display: flex;
  justify-content: flex-end;
  gap: 0.75rem;
}

.cancel-btn {
  background: white;
  color: #4a5568;
  border: 1px solid #e2e8f0;
  padding: 0.75rem 1.5rem;
  border-radius: 6px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.2s;
}

.cancel-btn:hover {
  background: #f7fafc;
  border-color: #cbd5e0;
}

.create-btn {
  background: #667eea;
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 6px;
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.create-btn:hover:not(:disabled) {
  background: #553c9a;
}

.create-btn:disabled {
  background: #a0aec0;
  cursor: not-allowed;
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .document-editor {
    flex-direction: column;
  }

  .documents-sidebar {
    width: 100%;
    height: 200px;
    order: 2;
  }

  .editor-main {
    order: 1;
    height: calc(100vh - 260px);
  }

  .comments-sidebar {
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    z-index: 100;
    transform: translateX(100%);
    transition: transform 0.3s;
  }

  .comments-sidebar.show {
    transform: translateX(0);
  }

  .editor-toolbar {
    flex-wrap: wrap;
    gap: 0.5rem;
  }

  .format-group {
    flex-wrap: wrap;
  }

  .rich-editor {
    padding: 1rem;
  }

  .comment-panel {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    max-width: 350px;
  }

  .template-options {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .editor-header {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }

  .doc-title-section {
    flex-direction: column;
    align-items: stretch;
    gap: 0.5rem;
  }

  .title-input {
    min-width: auto;
    font-size: 1.2rem;
  }

  .editor-actions {
    justify-content: space-between;
  }

  .rich-editor {
    font-size: 0.9rem;
  }
}

/* Scrollbar Styles */
.documents-list::-webkit-scrollbar,
.rich-editor::-webkit-scrollbar,
.comments-list::-webkit-scrollbar,
.modal-content::-webkit-scrollbar {
  width: 6px;
}

.documents-list::-webkit-scrollbar-track,
.rich-editor::-webkit-scrollbar-track,
.comments-list::-webkit-scrollbar-track,
.modal-content::-webkit-scrollbar-track {
  background: #f1f5f9;
  border-radius: 3px;
}

.documents-list::-webkit-scrollbar-thumb,
.rich-editor::-webkit-scrollbar-thumb,
.comments-list::-webkit-scrollbar-thumb,
.modal-content::-webkit-scrollbar-thumb {
  background: #cbd5e0;
  border-radius: 3px;
}

.documents-list::-webkit-scrollbar-thumb:hover,
.rich-editor::-webkit-scrollbar-thumb:hover,
.comments-list::-webkit-scrollbar-thumb:hover,
.modal-content::-webkit-scrollbar-thumb:hover {
  background: #a0aec0;
}

/* Accessibility */
@media (prefers-reduced-motion: reduce) {
  .create-first-doc {
    transition: none;
  }
  
  .new-doc-btn {
    transition: none;
  }
}

/* Search highlighting */
.rich-editor mark.highlight {
  background-color: #ffeb3b;
  color: #000;
  padding: 2px 4px;
  border-radius: 2px;
  font-weight: bold;
}

/* Find and replace panel */
.find-replace-panel {
  animation: slideDown 0.3s ease-out;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Enhanced formatting toolbar */
.format-btn.active {
  background: #007bff !important;
  color: white !important;
  box-shadow: inset 0 2px 4px rgba(0,0,0,0.2);
}

.format-btn:hover {
  background: #f8f9fa;
  border-color: #007bff;
  transform: translateY(-1px);
}

/* Document statistics */
.document-stats {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  transition: all 0.3s ease;
}

.document-stats:hover {
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

/* Reduced motion accessibility */
@media (prefers-reduced-motion: reduce) {
  .comments-sidebar {
    transition: none;
  }
}

/* Focus States */
.document-item:focus,
.format-btn:focus,
.template-option:focus {
  outline: 2px solid #667eea;
  outline-offset: 2px;
}
